 #root {
     display: flex;
     flex-direction: column;
     height: 100vh;
     overflow-x: hidden;
 }


 /* Fonte geral */
 body {
     font-family: 'Inter', 'Segoe UI', Roboto, sans-serif;
     font-size: 16px;
     line-height: 1.7;
     margin: 0;
     padding: 0;
     background-color: #f9f9f9;
     color: #1f1f1f;
     transition: background-color 0.4s ease, color 0.4s ease;
 }


 /* Modo escuro */
 body.dark-mode {
     background-color: #121212;
     color: #e4e4e4;
 }


 /* Container de controles */
 .controls {
     max-width: 1000px;
     margin: 40px auto;
     padding: 24px;
     background-color: #ffffff;
     border-radius: 12px;
     box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
     display: grid;
     gap: 20px;
     transition: background-color 0.4s ease;
 }


 body.dark-mode .controls {
     background-color: #1e1e1e;
     box-shadow: 0 2px 10px rgba(255, 255, 255, 0.05);
 }


 .controls label {
     font-weight: 600;
     color: inherit;
 }


 .controls button {
     padding: 12px 18px;
     background-color: #1d4ed8;
     color: #fff;
     font-weight: 600;
     border: none;
     border-radius: 8px;
     cursor: pointer;
     transition: background-color 0.3s ease;
 }


 .controls button:hover {
     background-color: #1e40af;
 }


 /* Seções */
 section {
     padding: 40px 30px;
     margin: 20px auto;
     max-width: 1200px;
     background-color: #ffffff;
     border-left: 5px solid #1d4ed8;
     border-radius: 12px;
     box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
     transition: background-color 0.4s ease;
 }


 body.dark-mode section {
     background-color: #1e1e1e;
     box-shadow: 0 2px 8px rgba(255, 255, 255, 0.05);
 }


 /* Títulos das seções */
 section h2 {
     font-size: 1.8em;
     margin-bottom: 20px;
     color: #1d4ed8;
     font-weight: 700;
     transition: color 0.4s ease;
 }


 body.dark-mode section h2 {
     color: #60a5fa;
 }


 /* Parágrafos */
 section p {
     font-size: 1.1em;
     margin-bottom: 20px;
 }


 /* Listas */
 section ul {
     list-style: disc;
     padding-left: 25px;
 }


 section ul li {
     margin-bottom: 10px;
     font-size: 1.05em;
 }


 /* Links */
 a {
     color: #1d4ed8;
     text-decoration: none;
     font-weight: 500;
     transition: color 0.4s ease;
 }


 a:hover {
     text-decoration: underline;
     color: #c71717;
 }


 /* Botão de tema */
 .toggle-theme-btn {
     position: fixed;
     top: 20px;
     right: 20px;
     padding: 10px 16px;
     background-color: #1d4ed8;
     color: white;
     border: none;
     border-radius: 8px;
     font-weight: 600;
     cursor: pointer;
     z-index: 1000;
     transition: background-color 0.4s ease;
 }


 body.dark-mode .toggle-theme-btn {
     background-color: #2563eb;
 }


 #three-container {
     margin: 20px 15px;
     margin-left: 70px;
     background-color: #1d4ed8;
     color: white;
     border-radius: 8px;
     z-index: 1;
     transition: background-color 0.4s ease;
     position: relative;
     width: 90vw;
     height: 100vh;
     overflow: hidden;
 }


 #three-container canvas {
     margin-left: 5px;
     display: flex;
     width: 100%;
     height: 100%;
     z-index: 1;
     position: absolute;
 }


 body.dark-mode #three-container {
     
     box-shadow: 0 2px 8px rgba(255, 255, 255, 0.05);
 }

 footer {
     background-color: #ffffff;
     color: #1f1f1f;
     text-align: center;
     padding: 16px 20px;
     font-size: 0.95rem;
     box-shadow: 0 -2px 8px rgba(0, 0, 0, 0.06);
     border-top: 3px solid #1d4ed8;
     margin-top: auto;
     transition: background-color 0.4s ease, color 0.4s ease;
 }

 body.dark-mode footer {
     background-color: #1e1e1e;
     color: #e4e4e4;
     box-shadow: 0 -2px 8px rgba(255, 255, 255, 0.05);
     border-top: 3px solid #60a5fa;
 }

 /* Responsividade */
 @media (max-width: 600px) {
     section {
         padding: 25px 20px;
     }


     .controls {
         padding: 20px;
     }


     .toggle-theme-btn {
         padding: 8px 14px;
         font-size: 14px;
     }
 }


footer ul {
    display: flex;
    justify-content: space-around;
}




footer li {
    list-style-type: none;
    cursor: pointer;
}


footer h4 {
    font-weight: normal;
}


footer div {
    border: rgba(0, 0, 255, 0.1) solid;
    border-radius: 25px;
    padding: 5px;
}


 /* Responsividade: ocupa tela cheia em celulares */
 @media (max-width: 768px) {
     #three-container {
         height: 400px;
         margin: 20px 15px;
     }
     footer ul {
    display: block;
    padding-left: 0;
}
 }


 @media (max-width: 480px) {
     #three-container {
         height: 320px;
     }
 }

